import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { PostPage } from '../pages/post/post';
import { AboutPage } from '../pages/about/about';
import { RoomListPage } from '../pages/room-list/room-list';
import { AddRoomFormPage } from '../pages/add-room-form/add-room-form';
import { RoomPage } from '../pages/room/room';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { IonicStorageModule } from '@ionic/storage';
import { FireDbProvider } from '../providers/fire-db/fire-db';
import { MsgboxProvider } from '../providers/msgbox/msgbox';
import { SessionProvider } from '../providers/session/session';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    PostPage,
    AboutPage,
    RoomListPage,
    AddRoomFormPage,
    RoomPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot(),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    PostPage,
    AboutPage,
    RoomListPage,
    AddRoomFormPage,
    RoomPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    FireDbProvider,
    MsgboxProvider,
    SessionProvider,
  ]
})
export class AppModule {}



