import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';

import { Storage } from '@ionic/storage';
import { FireDbProvider } from '../../providers/fire-db/fire-db';
import { MsgboxProvider } from '../../providers/msgbox/msgbox';
import { SessionProvider } from '../../providers/session/session';

/**
 * Generated class for the RoomPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-room',
  templateUrl: 'room.html',
})
export class RoomPage {
  room: any ;
  roomName: string = "";
  messages: any = [];
  username: any = "";
  newMsg: any = "";


  constructor(public navCtrl: NavController,
    private storage: Storage,
    private fire: FireDbProvider,
    private msg: MsgboxProvider,
    private session: SessionProvider,
    public events: Events,
    public navParams: NavParams) {
    this.room = navParams.get('room');
    this.roomName = this.room.name;
    if (this.session.getData("currentUser",false)){
      this.username = this.session.getData("currentUser",false).uid;
    }
    this.updateMsg();
    var that = this;
    this.fire.instance.ref('/rooms/' + this.roomName + "/").on('value',function(snapshot) {
      that.room = snapshot.val();
      that.updateMsg();
    });
  }

  ionViewDidLoad() {

  }

  updateMsg(){
    if (this.room.messages){
      var msgs = (<any>Object).values(this.room.messages);
      for (let m in msgs){
        if (msgs[m].sender == this.username){
          msgs[m].styleClass = "right";
        }
      }
      this.messages = msgs;
      setTimeout(()=>{
        if (document.getElementById("roomHeader")){
          var objDiv = document.getElementById("roomHeader").parentElement;
          objDiv.scrollTop = objDiv.scrollHeight;
        }
      },1000);
    } else {
      this.messages = [];
    }
  }

  sendMsg(){
    var that = this;
    var today = (<any>(new Date())) - 0;
    var roomMsgs = this.fire.instance.ref('/rooms/'+  this.roomName + "/messages/" + today);
    roomMsgs.set({
      sender: this.username,
      content: this.newMsg
    });
    this.newMsg = "";
  }

  onKeyPress(keycode){
    if(keycode == 13){
      this.sendMsg();
    }
  }

}



