import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AddRoomFormPage } from '../../pages/add-room-form/add-room-form';


import { Storage } from '@ionic/storage';
import { FireDbProvider } from '../../providers/fire-db/fire-db';
import { MsgboxProvider } from '../../providers/msgbox/msgbox';
import { SessionProvider } from '../../providers/session/session';

import { RoomPage } from '../../pages/room/room';
/**
 * Generated class for the RoomListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-room-list',
  templateUrl: 'room-list.html',
})
export class RoomListPage {
  rooms = [];

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    private storage: Storage,
    private fire: FireDbProvider,
    private msg: MsgboxProvider,
    private session: SessionProvider
  ) {
  }

  ionViewDidEnter() {
    var that = this;
    this.fire.instance.ref('/rooms/').on('value',function(snapshot) {
      if (snapshot.val()){
        that.rooms =(<any>Object).values(snapshot.val());
      } else {
        that.rooms = [];
      }

    });
  }

  addNewRoom(){
    this.navCtrl.push(AddRoomFormPage,{});
  }

  openRoom(room){
    this.navCtrl.push(RoomPage, {
      room: room
    });
  }
}



